import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { ColorPicker } from 'element-react';
import 'element-theme-default';
import { socketConnect } from 'reactjs-socket.io';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      color: ''
    }
    this.props.io.on('get_color', (socket) => {
      this.setState({ color: socket })
    })

  }



  render() {
    return (
      <div className="App" style={{ background: this.state.color }}>
        Set test host : <a href="react-socket.herokuapp.com">react-socket.herokuapp.com</a>
        <br/>
        use "get_color" key for getting the colour what you change 
        from here.
        <br/>
        <div className="block">
          <span className="demonstration">With default value</span>
          <ColorPicker value={this.state.color} onChange={(e) => {
            this.props.io.emit('set_color', e);
          }}></ColorPicker>
        </div>
      </div>
    );
  }
}

export default socketConnect(App);



